import { useState, useEffect } from 'react';

/**
 * Deck form component for creating/editing decks
 */
function DeckForm({ deck, onSubmit, onCancel }) {
  const [formData, setFormData] = useState({ title: '', description: '' });

  // Initialize form data when deck changes
  useEffect(() => {
    if (deck) {
      setFormData({
        title: deck.title || '',
        description: deck.description || ''
      });
    } else {
      setFormData({ title: '', description: '' });
    }
  }, [deck]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    
    if (!formData.title.trim()) {
      alert("Title is required!");
      return;
    }
    
    onSubmit(formData);
    setFormData({ title: '', description: '' });
  };

  return (
    <div className="overflow-y-auto h-[calc(70vh-70px)] pr-2">
      <div className="sticky top-0 z-20 note-paper py-4 px-4 mx-0 shadow-sm mb-6 flex justify-between items-center">
        <h2 className="text-2xl font-semibold text-gray-800 dark:text-gray-200">
          {deck && deck.id ? 'Edit Deck' : 'Create New Deck'}
        </h2>
        <div className="flex space-x-2">
          <button 
            onClick={onCancel}
            className="leather-button px-4 py-2"
          >
            Cancel
          </button>
        </div>
      </div>
      
      <form onSubmit={handleSubmit} className="space-y-4">
        <div>
          <label className="block text-gray-700 dark:text-gray-300 text-sm font-bold mb-2" htmlFor="title">
            Title
          </label>
          <input
            type="text"
            id="title"
            name="title"
            value={formData.title}
            onChange={handleChange}
            className="enhanced-input"
            placeholder="Deck Title"
          />
        </div>
        
        <div>
          <label className="block text-gray-700 dark:text-gray-300 text-sm font-bold mb-2" htmlFor="description">
            Description
          </label>
          <textarea
            id="description"
            name="description"
            value={formData.description}
            onChange={handleChange}
            rows="6"
            className="enhanced-textarea"
            placeholder="Deck Description"
          ></textarea>
        </div>
        
        <div className="flex justify-end">
          <button
            type="submit"
            className="leather-button py-2 px-6"
          >
            {deck && deck.id ? 'Update Deck' : 'Create Deck'}
          </button>
        </div>
      </form>
    </div>
  );
}

export default DeckForm;